.roboto-regular {
  font-family: Roboto, serif;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
}

.container {
  background: #fff;
  align-items: center;
  width: 100%;
  padding: 0 20px;
  display: flex;
}

.moyoLogo {
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-left: 30px;
  display: flex;
}

.nav-links {
  justify-content: center;
  align-items: center;
  height: 60px;
  margin-left: auto;
  display: flex;
  transform: translateX(-30px);
}

.nav-links ul {
  justify-content: center;
  align-items: center;
  list-style: none;
  display: flex;
}

.nav-links li + li {
  margin-left: 20px;
  line-height: 60px;
}

.nav-item {
  text-transform: uppercase;
  color: #000;
  flex-direction: column;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-item:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  background: #fff;
  justify-content: center;
  align-items: center;
  line-height: 60px;
  display: flex;
}

a.is-active {
  background-color: #fff;
  align-items: center;
  display: flex;
}

.is-active:after {
  content: "   ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width <= 1200px) {
  html {
    width: 100%;
  }
}

@media (width <= 1024px) {
  html {
    width: 100%;
  }
}
